video {
    object-fit: cover; 
    width: 100%;
    height: 100%; 
    position: fixed; 
    z-index: -1; 

}


.action-container {
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
  }
  
  .action-container > h1 {
    color: #fff;
    font-size: 75px;
    font-family: 'Staatliches', cursive;
    margin-top: -100px;
  }
  
  .action-container > p {
    margin-top: 8px;
    color: #fff;
    font-size: 32px;
    font-family: 'Raleway', sans-serif;
  }
  
  .action-btns {
    margin-top: 32px;
  }
  
  .action-btns .btn {
    margin: 15px;
  }
  
  .fa-play-circle {
    margin-left: 4px;
  }
  
  @media screen and (max-width: 960px) {
    .action-container > h1 {
      font-size: 70px;
      margin-top: -150px;
    }
  }
  
  @media screen and (max-width: 768px) {
    .action-container > h1 {
      font-size: 50px;
      margin-top: -100px;
    }
  
    .action-container > p {
      font-size: 30px;
    }
  
    .btn-mobile {
      display: block;
      text-decoration: none;
    }
  
    .btn {
      width: 100%;
    }
} 