.footer-container {
    background-color: #242424;
    padding: 4rem 0 2rem 0;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.footer-newsletter {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-bottom: 24px;
    padding: 24px;
    color: #fff;

}

.footer-newsletter>p {
    font-family: 'Raleway', sans-serif;

}

.footer-newsletter-heading {
    margin-bottom: 24px;
    font-size: 24px;
    font-weight: 600;
}

.footer-subscription-text {
    margin-bottom: 24px;
    font-size: 20px;

}

.footer-input {
    padding: 8px 20px;
    border-radius: 2px;
    margin-right: 10px;
    outline: none;
    border: none;
    font-size: 18px;
    margin-bottom: 16px;
    border: 1px solid #fff;
}

.footer-links {
    width: 100%;
    max-width: 1000px;
    display: flex;
    justify-content: center;
}

.footer-link-wrapper {
    display: flex;

}

.footer-link-items {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 15px;
    text-align: left;
    width: 160px;
    box-sizing: border-box;

}

.footer-link-items h2 {
    margin-bottom: 16px;

}

.footer-link-items>h2 {
    color: #fff;
}

.footer-link-items a {
    color: #fff;
    text-decoration: none;
    margin-bottom: 8px;

}

.footer-link-items a:hover {
    color: #e9e9e9;
    transition: 0.3 ease-out;
}

.footer-email-form h2 {
    margin-bottom: 32px;
}

.footer-input::placeholder {
    color: #b1b1b1
}

/* Social Icons */

.social-icon-link {
    color: #fff;
    font-size: 24px;
}

.social-media {
    max-width: 50%; 
    justify-content: space-between; 

}

.social-media-wrap {
    display: flex;
    justify-content: space-between; 
    width: 90%;
    max-width: 500px;
    margin: 40px auto 0 auto;

}

.social-icons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 240px;
}

.social-logo {
    color: #fff;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
    margin-bottom: 16px;
}

.website-rights {
    color: #fff; 
    margin-bottom: 16px;
    margin-top: 25px; 
}

@media screen and (max-width: 820px) {
    .footer-links {
        padding-top: 2rem;
    }
    .footer-input {
        width: 100%;
    } 

    .btn {
        width: 100px; 
    }

    .footer-link-wrapper {
        flex-direction: column; 
    }

    .social-media-wrap {
        float: left; 
    }
}